<template>
    
    <div>

        <Dialog @show="getAllProducts" v-model:visible="dialogVisible" modal @hide="props.onUpdateDone" header="Ürün Eşleme Grubu Güncelle" :style="{ width: '50vw' }" :breakpoints="{ '960px': '75vw', '641px': '95vw' }">
            <form @submit.prevent="update">

                <div>
                    <div class="d-flex flex-column">
                        <label class="form-label">İsim</label>
                        <InputText v-model="form.name" class="w-100" />
                    </div>

                    <Divider />

                    <div class="d-flex flex-column">
                        <label class="form-label">Sitede Görünecek İsim</label>
                        <InputText v-model="form.fe_name" class="w-100" />
                    </div>

                    <Divider />

                    <div class="d-flex flex-column">
                        <label class="form-label">Dahil Edilecek Ürünler</label>
                        <MultiSelect filter v-model="form.products" :options="allProducts" :maxSelectedLabels="5" optionLabel="name" optionValue="id" :virtualScrollerOptions="{ itemSize: 44 }" placeholder="Ürünleri Seç" class="w-100" />
                    </div>

                    <div class="separator my-10"></div>

                    <div>
                        <button type="submit" class="btn btn-primary" :class="{ 'opacity-25': form.processing }" :disabled="form.processing">Kaydet</button>
                        <button type="button" class="btn btn-light ms-5" @click="cancel()">Vazgeç</button>
                    </div>
                </div>

                <FormErrors :form="form"/>

            </form>
        </Dialog>
    </div>

</template>

<script setup>

import {ref} from "vue";
import { useForm } from "@inertiajs/vue3";
import Dialog from "primevue/dialog";
import InputText from 'primevue/inputtext';
import Divider from "primevue/divider";
import MultiSelect from "primevue/multiselect";

const props = defineProps({
    selectedProductPool:Object,
    onUpdateDone:{
        type:Function,
        required:true
    }
});


const dialogVisible = ref(true);
const allProducts = ref(null);

const getAllProducts = () => {

    if( !allProducts.value ){

        fetch(route('fetch.all-products'),{
            headers: {
                'Accept': 'application/json',
            }
        }).then(function (response) {
            return response.json();
        }).then((json) => {
            allProducts.value = json;
        });

    }

}

const form = useForm({
    name:props.selectedProductPool.name,
    fe_name:props.selectedProductPool.fe_name,
    products:props.selectedProductPool.products.map((row) => row.id)
});

const cancel = () => {
    form.reset();
    props.onUpdateDone();
};

const update = () => {
    form.put(route('product-pools.update',props.selectedProductPool),{
        onSuccess: () => {
            form.reset();
            props.onUpdateDone();
        },
    });
}


</script>